body{
  height: 100%;
  width: 100%;
  background-color: black;
  color: white;
  padding: 0;
  margin:0;
  /* border: 2px solid gold; */
}



#root{
  width: 100%;
  min-height: 100%;
}
.App{
  min-height:100%;
  display: flex;
  flex-direction:column;
  justify-content: center;
  align-items: center;
  padding: 5%;
  /* overflow-y: scroll; */
}
.main-container{
width: 100%;
  display: flex;
  flex-direction: column;
  
}
#Search-container{
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin:30px 0 30px 0;
}
#coin-container{
  padding: 20px 100px 20px 100px;
}
.coin-container-head{
  display: flex;
  justify-content: space-between;
  border-bottom: 2px solid greenyellow;
  margin-bottom: 20px;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-size: large;
  font-weight: 200;
  }
  .coin-container-head>div{
    width: 20%;
    margin-bottom:10px ;
  }
  .coin-container-body{
    width: 100%;
   
  }
.Cryptocoin{
display: flex;
justify-content: space-between;
border-bottom: 1px solid lightblue;
margin-bottom: 10px;
padding-bottom: 10px;
}
.Cryptocoin>div{
  width: 20%;
}
.green{
  color:greenyellow;
}
.red{
  color:red;
}
.footer{
  padding: 0 40px 0 40px;
  display: flex;
  justify-content: center;
}
.footer>button{
 border-radius: 10px;
 width: 100px;
 height: 30px;
 margin: 20px;
}
.footer>button:hover{
  background-color: rgb(53, 158, 207);
}
/* #coin-container-head{} */
#Search-container>input{
  background-color: azure;
  border-radius: 10px;
  height: 20px;
  width: 200px;
}

.Linegraph{
  min-height: 100%;
  display: flex;
  flex-direction: column;
  /* align-items: center; */
  justify-content: center;
  background-color: white;
  padding: 10px;
  width: 80%;
  height: 80%;
  /* overflow-y: hidden; */
}
.head-container{
  display: flex;
  justify-content: space-between;
  margin: 10px;
}
.graph-plot{
  padding: 10px;
}


@media screen and (max-width: 480px) {
  body{
    margin: 0;
  }
  .Market-Captial{
    display: none;
  }
  #coin-container{
    margin: 0;
    padding: 5px;
  }
  .Cryptocoin{
    margin: 0;
  }
  
}

